html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 22px;
  font-family: "Arial", sans-serif;
  color: #777777;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: 100%; }

.container__main {
  flex-grow: 1; }

.page-header__logo-link {
  display: block;
  width: 122px;
  height: 107px;
  background-color: #777777;
  margin-right: 10px; }

.page-header {
  padding: 0 10px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 10; }

.page-header__mobile-wrap {
  display: flex;
  width: 44%;
  justify-content: space-between;
  margin-right: 10px; }

.page-header__wrap {
  padding: 25px 0;
  margin: 0 auto;
  max-width: 1400px;
  width: 100%;
  border-bottom: 1px solid #696969;
  display: flex;
  justify-content: space-between; }

.page-header__block {
  display: block;
  height: 107px;
  width: 65%;
  background-color: #777777; }

.page-header__contacts {
  width: 53.39%;
  background-color: #777777;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 20px; }

.page-header__phone,
.page-header__mail {
  color: #ffffff;
  position: relative;
  transition: all 0.6s; }

.page-header__phone::before,
.page-header__mail::before {
  content: "";
  width: 0;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 50%;
  background-color: #fd8e0b;
  transition: all 0.3s; }

.page-header__phone:hover::before,
.page-header__mail:hover::before,
.page-header__phone:focus::before,
.page-header__mail:focus::before {
  width: 100%;
  left: 0;
  background-color: #fd8e0b; }

.page-header__phone:hover,
.page-header__mail:hover,
.page-header__phone:focus,
.page-header__mail:focus {
  color: #fd8e0b; }

@media (max-width: 768px) {
  .page-header__wrap {
    flex-direction: column; }
  .page-header__mobile-wrap {
    margin-right: 0;
    margin-bottom: 10px;
    width: 100%; }
  .page-header__block {
    width: 100%; }
  .page-header__contacts {
    width: 100%; } }

.first-section {
  display: flex;
  justify-content: space-between;
  padding: 17px 0 35px;
  max-width: 1420px;
  width: 100%;
  margin: 160px auto 0; }

.first-section__creation {
  background-color: #777777;
  color: #ffffff;
  padding: 13px 25px 8px 9px; }

.first-section__left-slogan {
  margin: 0;
  font-weight: 700; }

.first-section__creation-text {
  margin: 0; }

.first-section__left {
  width: 185px;
  margin-left: 10px;
  flex-shrink: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .first-section__left--fixed {
    position: relative; }
    .first-section__left--fixed .first-section__creation {
      position: fixed;
      top: 177px;
      width: 185px; }
    .first-section__left--fixed .first-section__left-down {
      margin-top: auto; }

.first-section__left-down {
  background-color: #777777;
  width: 100%;
  height: 132px;
  margin-top: 20px; }

.first-section__center {
  width: 78.95%;
  display: flex;
  flex-direction: column; }

.first-section__center-slogan {
  margin: 0 0 23px;
  font-weight: 700;
  font-size: 14px;
  line-height: 22px; }

.first-section__layout {
  background-color: #dcdcdc;
  padding: 15px 17px;
  margin: 0 10px 20px; }

.first-section__feature-name {
  font-weight: 700;
  margin-right: 3px; }

.first-section__feature-item span {
  font-weight: 700; }

.first-section__feature-item {
  position: relative;
  display: flex; }

.first-section__feature-item::before {
  position: absolute;
  content: "";
  left: -6px;
  top: 10px;
  width: 3px;
  height: 3px;
  background-color: #777777; }

.first-section__feature-list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin: 0 0 20px 5.65%;
  display: flex;
  flex-direction: column; }

.first-section__cards-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  flex-grow: 1;
  height: 100%; }

.first-section__card {
  width: 136px;
  height: 153px;
  background-color: #dcdcdc;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
  margin: 0 10px 20px;
  position: relative;
  transition-property: bottom, box-shadow;
  transition-duration: 0.5s;
  transition-delay: 0.2s;
  bottom: 0;
  box-shadow: none; }

.first-section__card:hover {
  bottom: 5px;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5); }

.first-section__card .first-section__bag-icon {
  color: #000000;
  transition-property: color;
  transition-duration: 0.5s;
  transition-delay: 0.2s; }

.first-section__card:hover .first-section__bag-icon {
  color: #fd8e0b; }

.first-section__buy-button {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  width: 22px;
  height: 22px; }

.first-section__buy-button:hover .first-section__bag-icon,
.first-section__buy-button:focus .first-section__bag-icon {
  color: #f31453; }

.first-section__buy-button:active .first-section__bag-icon {
  opacity: 0.7; }

.first-section__left-text-column,
.first-section__right-text-column {
  background-color: #dcdcdc;
  padding: 15px 15px 60px;
  width: 50%; }

.first-section__left-text-column {
  margin-right: 20px; }

.first-section__text-wrap {
  margin: 0 10px;
  display: flex; }

.first-section__center-text {
  margin: 0 0 30px; }
  .first-section__center-text--1 {
    margin-bottom: 21px; }
  .first-section__center-text--2 {
    margin-bottom: 22px; }
  .first-section__center-text--3 {
    margin-bottom: 0; }

@media (max-width: 768px) {
  .first-section__text-wrap {
    margin-left: 0;
    flex-direction: column; }
  .first-section__left-text-column {
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%; }
  .first-section__right-text-column {
    width: 100%; }
  .first-section__cards-list {
    justify-content: center; }
  .first-section {
    margin-top: 252px;
    flex-direction: column; }
  .first-section__creation {
    width: 100%;
    margin-right: 10px; }
  .first-section__center {
    width: 100%; }
  .first-section__left-down {
    margin-top: 0;
    width: 40%; }
  .first-section__left {
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    margin-right: 10px;
    margin-bottom: 20px; }
    .first-section__left--fixed {
      flex-direction: row; }
      .first-section__left--fixed .first-section__creation {
        position: static;
        width: 100%; }
      .first-section__left--fixed .first-section__left-down {
        margin-top: 0;
        width: 40%; } }

.page-footer__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 180px;
  padding: 8px 9%; }

.page-footer__nav {
  background-color: #777777;
  width: 31%;
  min-width: 250px; }

.page-footer__nav-link {
  color: #ffffff; }

.page-footer__column-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 54%; }

.page-footer__copyrights,
.page-footer__contacts {
  background-color: #777777;
  height: 75px; }

.page-footer__wrap {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #696969;
  padding: 33px 0; }

.page-footer__rates-list {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #777777;
  height: 180px;
  padding: 10px 5px 7px 24px;
  width: 50%;
  min-width: 250px;
  margin-right: 30px; }

.page-footer__rate-link {
  color: #ffffff;
  position: relative; }

.page-footer__rate-item {
  margin-left: 10px; }

.page-footer__rate-link::before {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  background-color: #ffffff;
  top: 5px;
  left: -11px; }

.page-footer {
  padding: 0 10px;
  margin: 0 auto;
  max-width: 1420px;
  width: 100%; }

.page-footer__mobile-wrap {
  display: flex;
  width: 66%; }

.page-footer__nav-link:hover,
.page-footer__nav-link:focus,
.page-footer__rate-link:hover,
.page-footer__rate-link:focus {
  color: #fd8e0b; }

.page-footer__rate-link:hover::before,
.page-footer__rate-link:focus::before {
  background-color: #fd8e0b; }

.page-footer__nav-link:active,
.page-footer__rate-link:active {
  opacity: 0.7; }

@media (max-width: 768px) {
  .page-footer__wrap {
    flex-direction: column; }
  .page-footer__nav {
    width: 100%;
    margin-bottom: 20px; }
  .page-footer__nav-list {
    align-items: center; }
  .page-footer__mobile-wrap {
    width: 100%;
    flex-direction: column; }
  .page-footer__rates-list {
    min-width: auto;
    height: auto;
    width: 100%;
    margin-bottom: 20px;
    margin-right: 0; }
  .page-footer__column-wrap {
    width: 100%; }
  .page-footer__copyrights {
    margin-bottom: 20px; } }
